import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
export default new Vuex.Store({
    state: {
        _token: 'E6p7tHBCC2vjmx8ZDQ33dXDxMOOS1sFpaue0aWEN',
        districts: {},
        specialties: {},
        myteam: {},
        auth: {
            id: 0,
            firstname: '',
            surname: '',
            avatar: '',
            since: '',
            phone: '',
            email: '',
            confirmed: false,
            district: 0,
            about: '',
            birthday: '',
            group_id: '',
            aBalance: 0,
            pBalance: 0,
            notifications: [],
            created_at: ''
        },
        searchTypeCurrent: 'all',
        searchTypes: {
            all: 'Поиск по сайту',
            catalog: 'Поиск по товарам',
            service: 'Поиск по услугам',
        },
        ddStates: {
            balance: false,
            notifications: false,
            profile: false,
            search: false
        }
    },
    getters: {
        fullname(state) {
            return state.auth.firstname + ' ' + state.auth.surname;
        },
        pBalance(state) {
            return state.auth.pBalance.toString().replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1,');
        },
        aBalance(state) {
            return state.auth.aBalance.toString().replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1,');
        }
    },
    mutations: {
        authentication: (state, data) => {
            state.auth = data;
        },
        loadDistricts: (state, data) => {
            state.districts = data;
        },
        loadSpecialties: (state, data) => {
            state.specialties = data;
        },
        loadMyTeam: (state, data) => {
            state.myteam = data;
        },

        selectSearchType: (state, name) => {
            state.searchTypeCurrent = name;
            state.ddStates.search = false;
        },
        doLogOut: (state) => {
            state.auth.id = 0;
        },
        SOCKET_CONNECT: (state) => {
            state.connect = true;
        },
        SOCKET_DISCONNECT: (state) => {
            state.connect = false;
        }
    }
})



