import Vue from 'vue';
import VueResource from 'vue-resource';

let moment = require('moment');
require('moment/locale/ru');

Vue.use(require('vue-moment'), { moment });

// import VueSocketIo from 'vue-socket.io';
import {
    Radio,
    Checkbox,
    Input,
    Select,
    Upload,
    Rate,
    Dialog,
    Tooltip,
    Option,
    OptionGroup,
    // DatePicker
} from 'element-ui'
import lang from 'element-ui/lib/locale/lang/ru-RU'
import locale from 'element-ui/lib/locale'
locale.use(lang);

import App from './components/App.vue';
import store from './store/store';
import router from './router/router'
import VueChatScroll from './components/global/down-scroll/vue-scroll'
// Vue.http.headers.common['X-CSRF-TOKEN'] = $('meta[name="token"]').attr('value');

Vue.config.productionTip = false;
// Vue.use(VueSocketIo, 'http://localhost:3000/', store);
Vue.use(Radio, { locale });
Vue.use(Checkbox, { locale });
Vue.use(Input, { locale });
Vue.use(Select, { locale });
Vue.use(Upload, { locale });
Vue.use(Rate, { locale });
Vue.use(Dialog, { locale });
Vue.use(Tooltip, { locale });
Vue.use(Option, { locale });
Vue.use(OptionGroup, { locale });
// Vue.use(DatePicker, { locale });
Vue.use(VueChatScroll);
Vue.use(VueResource);

// Vue.http.headers.common['X-CSRF-TOKEN'] = 'E6p7tHBCC2vjmx8ZDQ33dXDxMOOS1sFpaue0aWEN';
import { mapMutations } from 'vuex'

new Vue({
    el: '#app',
    store,
    router,
    template: '<App />',
    components: { App },
    mounted() {
        document.body.removeAttribute('style');
    },
    created() {
        //Загрузка данных пользователя
        this.$http.get('/api/user').then(response => {
            this.authentication(response.body);
        }, response => {
            console.log(response)
        });
        //Загрузка данных по районам
        this.$http.get('/api/districts').then(response => {
            this.loadDistricts(response.body);
        }, response => {
            console.log(response)
        });
        //Загрузка данных по специализациям
        this.$http.get('/api/specialties').then(response => {
            this.loadSpecialties(response.body);
        }, response => {
            console.log(response)
        });
        //Загрузка моей команды
        this.$http.get('/api/myteam').then(response => {
            this.loadMyTeam(response.body);
        }, response => {
            console.log(response)
        });
    },
    methods: {
        ...mapMutations([
            'authentication',
            'loadDistricts',
            'loadSpecialties',
            'loadMyTeam'
        ])
    },

    sockets: {
        connect: function() {
            console.log('connected');
        }
    }

});


